import Grid from '@material-ui/core/Grid'
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from '@material-ui/core/styles'
import React from 'react'
import EnhancedTable from '../../components/Enhanced/Table/Table'
import TableWrapper from '../../components/TableWrapper'
import { STOCK_TRANSACTION_TABLE } from '../../graphql/mutation/local'
import { STOCK_TRANSACTION_TABLE as STOCK_TRANSACTION_TABLE_QUERY } from '../../graphql/query/local'
import { STOCK_TRANSACTIONS } from '../../graphql/query/remote'
import { defaultState } from '../../graphql/resolvers'
import commanRender, { RENDER_VALUE } from '../../utils/commanRender'
import getContactJSX from '../../utils/getContactJSX'

const styles = (theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
    },
    fab: {
      position: 'fixed',
      width: '4rem',
      bottom: theme.spacing.unit * 2,
      right: theme.spacing.unit * 2,
    },
  })

export const stockTransactionColumnData = [
  {
    id: 'createdAt',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Created At',
  },
  {
    id: 'stock',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Stock Name',
  },
  {
    id: 'quantity',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Quantity',
  },
  {
    id: 'type',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Transaction Type',
  },
  {
    id: 'available',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Available',
  },
  {
    id: 'supplier',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Supplier/Consumer',
  },
  {
    id: 'note',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: (
      <span style={{ paddingLeft: '4rem', paddingRight: '4rem' }}>Note</span>
    ),
  },
]

export const stockTransactionExclude = [
  'id',
  'createdAt',
  'updatedAt',
  '__typename',
]

interface IStockItem extends WithStyles<typeof styles> {
  history?: any
  query?: any
  getQueryVariables?: any
}

export const TransactionPage = withStyles(styles)((props: IStockItem) => {
  const { classes, history, ...restProps } = props
  const { stockTransactionTable } = defaultState
  return (
    <div>
      <Grid container={true} className={classes.root}>
        <Grid item={true} xs={12}>
          <TableWrapper
            tableMutation={STOCK_TRANSACTION_TABLE}
            tableQuery={STOCK_TRANSACTION_TABLE_QUERY}
            dataQuery={restProps.query || STOCK_TRANSACTIONS}
            getQueryVariables={restProps.getQueryVariables}
            defaultState={stockTransactionTable}
          >
            {(
              tableSettings: typeof stockTransactionTable,
              lmutate: Function,
              { loading, data, refetch }: IGraphqlData,
            ) => {
              const transactions = data[Object.keys(data)[0]]
              return (
                <EnhancedTable
                  {...restProps}
                  loading={loading}
                  data={transactions.edges}
                  title='Stock Transactions'
                  settings={tableSettings}
                  count={transactions.totalCount}
                  columnData={stockTransactionColumnData}
                  exclude={stockTransactionExclude}
                  render={commanRender({
                    match: 'type',
                    alt: true,
                    altValue: 'DEBIT',
                    altColor: '#eee',
                    renderValue: ({ key, value }: any) => {
                      if (key === 'contact') {
                        return { value: getContactJSX(value) }
                      }
                      const result = RENDER_VALUE({
                        key,
                        value,
                        skip: ['contact'],
                      })
                      return {
                        ...result,
                        value: String(value).length ? result.value : '-',
                      }
                    },
                  })}
                  mutate={async args => {
                    await lmutate({ variables: args })
                    // refetch();
                  }}
                />
              )
            }}
          </TableWrapper>
        </Grid>
      </Grid>
    </div>
  )
})
