import Grid from '@material-ui/core/Grid'
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from '@material-ui/core/styles'
import gql from 'graphql-tag'
import React from 'react'
import FormDialog from '../../components/Dialogs/FormDialog'
import EnhancedTable from '../../components/Enhanced/Table/Table'
import Mutation from '../../components/Mutation'
import TableWrapper from '../../components/TableWrapper'
import { STOCK_ITEM_TABLE } from '../../graphql/mutation/local'
import { DELETE_STOCK_ITEMS } from '../../graphql/mutation/remote'
import { STOCK_ITEM_TABLE as STOCK_ITEM_TABLE_QUERY } from '../../graphql/query/local'
import { STOCK_ITEMS } from '../../graphql/query/remote'
import { defaultState } from '../../graphql/resolvers'
import { Routes } from '../../Router'

const styles = (theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
    },
    fab: {
      position: 'fixed',
      width: '4rem',
      bottom: theme.spacing.unit * 2,
      right: theme.spacing.unit * 2,
    },
  })

export const stockItemColumnData = [
  {
    id: 'createdAt',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Created At',
  },
  {
    id: 'name',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Name',
  },
  {
    id: 'unit',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Unit',
  },
  {
    id: 'available',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Available',
  },
]

export const stockItemExclude = ['id', 'createdAt', 'updatedAt', '__typename']

interface IStockItem extends WithStyles<typeof styles> {
  history?: any
  optionable?: boolean
}

const STOCK_CREDIT = gql`
  mutation createTransaction($data: TransactionCreateInput!) {
    createTransaction(data: $data) {
      id
      createdAt
      stock {
        name
      }
      supplier {
        username
      }
    }
  }
`
const defaults: IFormGenerator = {
  quantity: {
    type: 'number',
    default: 0,
    // startAdornment: <InputAdornment position='start'>â‚¹</InputAdornment>,
  },
  note: {
    type: 'text',
    default: '',
  },
  type: {
    type: 'select',
    default: [
      { key: 'CREDIT [+]', value: 'CREDIT' },
      { key: 'DEBIT [-]', value: 'DEBIT' },
    ],
    current: 'CREDIT',
  },
}
export const StockPage = withStyles(styles)(
  (props: IStockItem & { user?: any }) => {
    const { classes, history, user, ...restProps } = props
    const { stockItemTable } = defaultState
    return (
      <div>
        <Grid container={true} className={classes.root}>
          <Grid item={true} xs={12}>
            <Mutation mutation={DELETE_STOCK_ITEMS}>
              {(
                deleteStockItems: Function,
                { loading: dloading }: IGraphqlData,
              ) => {
                return (
                  <TableWrapper
                    tableMutation={STOCK_ITEM_TABLE}
                    tableQuery={STOCK_ITEM_TABLE_QUERY}
                    dataQuery={STOCK_ITEMS}
                    defaultState={stockItemTable}
                  >
                    {(
                      tableSettings: typeof stockItemTable,
                      lmutate: Function,
                      { loading, data, refetch }: IGraphqlData,
                    ) => {
                      const options = props.optionable && {
                        credit: ({
                          selected,
                          onCredit,
                        }: {
                          selected: string
                          onCredit: any
                        }) => (
                          <FormDialog
                            mutation={STOCK_CREDIT}
                            defaults={defaults}
                            positiveHandler={(m: any, e: any) =>
                              onCredit &&
                              onCredit(m, {
                                data: {
                                  ...e,
                                  stockId: selected,
                                  supplierId: user.id,
                                },
                              })
                            }
                          />
                        ),
                        options: {
                          onCredit: async (fn: Function, args: any) => {
                            await fn({ variables: args })
                            // refetch();
                          },
                          onView: (e: any) =>
                            history.push(Routes.Stock.detail({ id: e })),
                        },
                      }
                      return (
                        <EnhancedTable
                          {...restProps}
                          loading={loading || dloading}
                          data={data.stocks.edges}
                          title='Stock Items'
                          settings={tableSettings}
                          count={data.stocks.totalCount}
                          columnData={stockItemColumnData}
                          exclude={stockItemExclude}
                          {...options}
                          mutate={async (args: any) => {
                            await lmutate({ variables: args })
                            // refetch();
                          }}
                        />
                      )
                    }}
                  </TableWrapper>
                )
              }}
            </Mutation>
          </Grid>
        </Grid>
      </div>
    )
  },
)
