import React, { PureComponent } from 'react'
import _404 from '../components/404'
import Tabs from '../components/Tabs'
import { STOCK_ENHANCED_TABS } from '../graphql/mutation/local'
import { STOCK_ENHANCED_TABS as STOCK_ENHANCED_TABS_QUERY } from '../graphql/query/local'
import { StockPage } from './Stock/StockPage'
import { TransactionPage } from './Stock/Transaction'

class StockDashboard extends PureComponent {
  render() {
    const defaults = [
      {
        tab: 'Stock',
        container: (
          <StockPage
            {...this.props}
            optionable={(this.props as any).me.stockPermission}
          />
        ),
      },
      // {
      //   tab: 'Supplier',
      //   container: <SupplierPage {...this.props} />,
      // },
      {
        tab: 'Transaction',
        container: <TransactionPage {...this.props} />,
      },
    ]
    const { me }: any = this.props
    if (me && me.stockPermission) {
      return (
        <Tabs
          defaults={defaults}
          localStateMutation={STOCK_ENHANCED_TABS}
          localStateQuery={STOCK_ENHANCED_TABS_QUERY}
        />
      )
    }
    return <_404 />
  }
}

export default StockDashboard
