import AppBar from '@material-ui/core/AppBar'
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from '@material-ui/core/styles'
import Tab from '@material-ui/core/Tab'
import Tabs from '@material-ui/core/Tabs'
import Typography from '@material-ui/core/Typography'
import React from 'react'
import SwipeableViews from 'react-swipeable-views'
import Mutation from '../components/Mutation'
import Query from '../components/Query'

function TabContainer({ children, dir }: any) {
  return (
    <Typography component='div' dir={dir}>
      {children}
    </Typography>
  )
}

const styles = (theme: Theme) =>
  createStyles({
    root: {
      backgroundColor: theme.palette.background.paper,
      width: '100%',
    },
    app: {
      marginTop: theme.spacing.unit * 8,
    },
    content: {
      marginTop: theme.spacing.unit * 5,
    },
    selected: {
      backgroundColor: theme.palette.secondary.light,
      color: theme.palette.secondary.contrastText,
    },
  })

interface IEnhancedTabsProps extends WithStyles<typeof styles, true> {
  defaults: any[]
  localStateQuery: any
  localStateMutation: any
}

const EnhancedTabs = withStyles(styles, { withTheme: true })(
  class extends React.PureComponent<IEnhancedTabsProps> {
    render() {
      const { classes, theme } = this.props
      const { defaults, localStateQuery, localStateMutation } = this.props
      const result: { tabs: any[]; containers: any[] } = {
        tabs: [],
        containers: [],
      }
      defaults.map((e: any) => {
        result.tabs.push(
          <Tab
            label={e.tab}
            key={e.tab}
            classes={{ selected: classes.selected }}
          />,
        )
        result.containers.push(
          <TabContainer dir={theme.direction} key={e.tab + '_container'}>
            {e.container}
          </TabContainer>,
        )
        return null
      })

      return (
        <Mutation mutation={localStateMutation}>
          {(lmutate: Function) => {
            return (
              <Query query={localStateQuery}>
                {({ data }: any) => {
                  const enhancedTabs = data[Object.keys(data)[0]]
                  return (
                    <div className={classes.root}>
                      <AppBar color='default' className={classes.app}>
                        <Tabs
                          value={enhancedTabs.value}
                          onChange={(_, value) =>
                            lmutate({ variables: { value } })
                          }
                          indicatorColor='secondary'
                        >
                          {result.tabs}
                        </Tabs>
                      </AppBar>
                      <SwipeableViews
                        className={classes.content}
                        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                        index={enhancedTabs.value}
                        onChangeIndex={value =>
                          lmutate({ variables: { value } })
                        }
                      >
                        {result.containers}
                      </SwipeableViews>
                    </div>
                  )
                }}
              </Query>
            )
          }}
        </Mutation>
      )
    }
  },
)
export default EnhancedTabs
